<template>
  <v-container id="login" class="fill-height justify-center" tag="section">
    <v-row justify="center">
      <v-col lg="11" sm="8" xl="7">
        <v-card class="elevation-4">
          <v-row>
            <v-col lg="5" class="pr-0">
              <div class="pa-7 pa-sm-12">
                <img src="@/assets/images/odklogo.png" />
                <h2 class="font-weight-bold mt-4 blue-grey--text text--darken-2">重置密码</h2>
                <h6 class="subtitle-1">
                  已经有账户?
                  <a href="#" class @click="gotologin">登陆</a>
                </h6>

                <v-form ref="form" lazy-validation >
                  <v-text-field
                    v-model="fname"
                    :rules="fnameRules"
                    label="用户名"
                    class="mt-4"
                    required
                    outlined
                  ></v-text-field>
                  
                  <v-text-field
                    v-model="password"
                    :rules="passwordRules"
                    label="密码"
                    required
                    outlined
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show1 ? 'text' : 'password'"
                  ></v-text-field>

                  <v-text-field
                    v-model="password1"
                    :rules="passwordRules"
                    label="请再次输入密码"
                    required
                    outlined
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show1 ? 'text' : 'password'"
                  ></v-text-field>

                  <v-text-field
                    v-model="email"
                    :rules="emailRules"
                    label="QQ邮箱"
                    required
                    outlined
                  ></v-text-field>
                  <v-btn
                    color="info"
                    block
                    class="mr-4"
                    @click="sendemailcode"
                  >发送验证码</v-btn>

                  <v-text-field
                    v-model="emailcode"
                    label="邮箱验证码"
                    required
                    outlined
                  ></v-text-field>

                  <v-btn
                    color="info"
                    block
                    class="mr-4"
                    submit
                    @click="submit"
                  >重置密码</v-btn>
                </v-form>

                
                
              </div>
            </v-col>
            <v-col lg="7" class="info d-none d-md-flex align-center justify-center">
              <div class="d-none d-sm-block">
                <div class="d-flex align-center pa-10">
                  <div>
                    <h2
                      class="display-1 white--text font-weight-medium"
                    >老王帮你做交易</h2>
                    <h6
                      class="subtitle-1 mt-4 white--text op-5 font-weight-regular"
                    >老王帮你做交易.</h6>
                    <v-btn class="mt-4 text-capitalize" x-large outlined color="white">了解更多</v-btn>
                  </div>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import * as api from "@/apis/wswebapi"
import md5 from 'js-md5';
export default {
  name: "BoxedRegister",
  data: () => ({
    password: "",
    show1: false,
    passwordRules: [
      v => !!v || "请输入密码",
      v => (v && v.length <= 20) || "密码必须小于20位"
    ],
    password1:"",

    email: "",
    emailRules: [
      v => !!v || "请输入邮箱",
      v => /.+@.+\..+/.test(v) || "请输入邮箱"
    ],
    emailcode:"",
    checkbox: false,
    fname: "",
    fnameRules: [
      v => !!v || "请输入用户名",
      v => (v && v.length <=20) || "请输入用户名"
    ]
  }),
  methods: {
    gotologin(){
      this.$router.push({path:"/user/login"});
    },
    sendemailcode(){
      if (this.email){
        api.GetEmailcode({mailto:this.email}).then(res=>{
          if (res.code==200){
            this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
          }else{
            this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
          }
        })
      }else{
        this.$store.dispatch("msglog/push_msg",{message:"请输入有效邮箱",type:"error"})
      }
      
    },
    submit() {
      this.$refs.form.validate();
      if (this.$refs.form.validate(true)) {
        if (this.password==this.password1){
          let params ={username:this.fname,password:md5(this.password),email:this.email,emailcode:this.emailcode};
          api.ResetPassword(params).then(res=>{
            if (res.code==200){
              this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
            }else{
              this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
            }
          })
        }else{
          this.$store.dispatch("msglog/push_msg",{message:"两次输入密码不一致",type:"error"})
        }
        
      }
    }
  }
};
</script>
